import React, { createContext, useContext, useEffect, useState } from 'react';

//     Telegram
const TelegramContext = createContext(null);

export function TelegramProvider({ children }) {
  const [telegramData, setTelegramData] = useState({
    user: null,
    initData: null,
    webApp: null,
    isInitialized: false,
    isLoading: true,
    error: null
  });

  useEffect(() => {
    const initTelegram = async () => {
      try {
        //   Telegram WebApp API
        if (window.Telegram && window.Telegram.WebApp) {
          const webApp = window.Telegram.WebApp;
          
          //  
          webApp.ready();
          
          //   
          const user = webApp.initDataUnsafe?.user || null;
          const initData = webApp.initData || null;
          
          console.log('Telegram WebApp initialized:', { user, initData });
          
          //    
          setTelegramData({
            user,
            initData,
            webApp,
            isInitialized: true,
            isLoading: false,
            error: null
          });
          
          //   
          webApp.expand(); //     
          webApp.enableClosingConfirmation(); //    
          
          //   -
          const bgColor = '#020203';
          webApp.setBackgroundColor(bgColor);
        } else {
          //      Telegram
          console.log('Telegram WebApp is not available. Running in standalone mode.');
          setTelegramData({
            user: { first_name: '' },
            initData: null,
            webApp: null,
            isInitialized: false,
            isLoading: false,
            error: 'Telegram WebApp is not available'
          });
        }
      } catch (error) {
        console.error('Error initializing Telegram WebApp:', error);
        setTelegramData({
          user: { first_name: '' },
          initData: null,
          webApp: null,
          isInitialized: false,
          isLoading: false,
          error: error.message
        });
      }
    };

    initTelegram();
  }, []);

  //     Telegram WebApp API
  const telegramMethods = {
    //      Telegram
    showAlert: (message) => {
      if (telegramData.webApp) {
        telegramData.webApp.showAlert(message);
      } else {
        alert(message);
      }
    },
    
    //  
    close: () => {
      if (telegramData.webApp) {
        telegramData.webApp.close();
      }
    },
    
    //    
    sendData: (data) => {
      if (telegramData.webApp) {
        telegramData.webApp.sendData(JSON.stringify(data));
      }
    },
    
    //     Telegram
    openLink: (url) => {
      if (telegramData.webApp) {
        telegramData.webApp.openLink(url);
      } else {
        window.open(url, '_blank');
      }
    }
  };

  return (
    <TelegramContext.Provider value={{ ...telegramData, ...telegramMethods }}>
      {children}
    </TelegramContext.Provider>
  );
}

//     Telegram
export function useTelegram() {
  const context = useContext(TelegramContext);
  if (!context) {
    throw new Error('useTelegram must be used within a TelegramProvider');
  }
  return context;
} 